import { useDispatch, useSelector } from 'react-redux'
import React, { useState, useEffect } from 'react'
import {deleteTask, listTasks} from '../actions/taskActions'
//import "../Style/style.css";
import axios from "axios";

const TaskList = ({history}) => {

	const dispatch = useDispatch()
	const userLogin = useSelector(state => state.userLogin);
	const taskList = useSelector(state => state.taskList);
	const { error, loading, userInfo } = userLogin;


	useEffect(() => {

		if (userInfo) {

			dispatch(listTasks())
        } 
		// else {
        //     history.push('/login')
        // }

	}, [dispatch, userInfo]);


	// const updateRequest = (id) => {
	// 	console.log("data", data.data.length);
	// 	dispatch({
	// 		type : "UPDATE_TASK_REQUEST",
	// 		payload : id 
	// 	})
	// }

	const deleteHandler = (e, id) => {
		e.preventDefault();

		dispatch(deleteTask(id))

	}

	return (
		<div className="col-md-8">
			<div className="h-100 p-4 bg-light border rounded-3">
				<h2 className="text-center">Task List</h2> <br />
				{userLogin ?
					(taskList.loading ? <span className="loader">LOADING...</span> :
						(taskList.tasks ?
							(<>
								<div className="table-responsive table-fixed">
									<table className="table table-striped">
										<thead className="thead-dark">
											<tr>
												<th scope="col">#</th>
												<th scope="col">Task</th>
												<th scope="col">Priority</th>
												<th scope="col">Status</th>
												<th scope="col">Action</th>
											</tr>
										</thead>
										<tbody>
											{taskList.tasks.length > 0 &&
												taskList.tasks.map((task) => (
													<tr key={task.id}>
														<th scope="row">{task.id}</th>
														<td>{task.title}</td>
														<td>{task.priority}</td>
														<td>{task.status === "0" ? "Pending" : "Completed"}</td>
														<td>
															<button
																className="btn btn-warning"
																// onClick={() => upadetRequest(task.id)}
															>
																Update
															</button> {" "}
															<button
																className="btn btn-danger"
																onClick={(e) => deleteHandler(e, task.id)}
															>
																Delete
															</button>
														</td>

													</tr>)
												)
											}
										</tbody>
									</table>
								</div>
							</>):<span className="error">AN ERROR HAS OCCURED</span> 
						)
					)
					:
					(
						<h1 className="text-center">Please Login</h1>
					)}
			</div>
		</div >
	);
};

export default TaskList;


